<template>
  <component :is="node.tag" v-bind="node.props" v-on="getEventHandlers(node)">
    <template v-if="node.children && node.children.length > 0">
      <recursive-render
        v-for="(item, index) in node.children"
        :key="index"
        :node="item"
      />
    </template>
    {{node.text}}
  </component>
</template>

<script>
import imgViewer from "@/components/img-viewer.vue";
import slideBox from "@/components/slide-box.vue";
import AutoVideo from "@/components/auto-video.vue";
import swipeBox from "@/components/swipe-box.vue";
export default {
  name: "RecursiveRender",
//   inject: ["resetSlide"],
  inject: [],
  data() {
    return {};
  },
  components: {
    imgViewer,
    slideBox,
    AutoVideo,
    swipeBox
  },
  props: {
    node: {
      type: Object,
      default: () => {},
    },
  },
  methods: {
    getEventHandlers(node) {
      // 动态生成事件处理对象
      const events = {};
      if (node.events?.mouseleave === "resetSlide") {
        // 使用箭头函数包裹以延迟执行
        events.mouseleave = () => this.resetSlide(node.props?.slideName || "");
      }
      return events;
    },
  },
};
</script>
