<template>
  <div class="position-relative w-100 overflow-hidden d-flex swipe-box">
    <img
      loading="lazy"
      :class="swipeIndex == 0 ? 'disabled' : ''"
      class="img-icon arrow-left hover-opacity-8"
      @click="onSwipe(0)"
      :src="hashUrl('aftersale/arrowleft.png')"
    />
    <img
      loading="lazy"
      :class="swipeIndex == images.length - 1 ? 'disabled' : ''"
      class="img-icon arrow-right hover-opacity-8"
      @click="onSwipe(1)"
      :src="hashUrl('aftersale/arrowright.png')"
    />
    <template v-for="(temp, idx) in images">
      <img
        loading="lazy"
        :key="idx"
        class="w-100 swipe-img"
        :class="idx < swipeIndex ? 'swiped' : ''"
        :src="temp"
      />
    </template>
  </div>
</template>
      
<script>
export default {
  name: "SwipeBox",
  props: {
    images: {
      type: Array,
      default: () => [],
    },
  },
  data() {
    return {
        swipeIndex: 0,
    };
  },
  methods: {
    onSwipe(val) {
      if(val == 0) {
        this.swipeIndex = this.swipeIndex > 0 ? this.swipeIndex - 1 : 0;
      } else {
        this.swipeIndex = this.swipeIndex < this.images.length - 1 ? this.swipeIndex + 1 : this.images.length - 1;
      }
    },
  },
  mounted() {
  },
};
</script>
      