<template>
  <div
    class="slide-box" @mouseleave="resetSlide"
  >
    <template v-for="(temp, idx) in images">
      <img
        loading="lazy"
        :key="idx"
        class="slide-img"
        :class="getClass(idx)"
        :src="temp"
        @mouseenter="onHover(idx)"
      />
    </template>
  </div>
</template>
    
    <script>
    import { SLIDE_INDICES } from "@/constant";
export default {
  name: "SlideBox",
  props: {
    slideName: String,
    images: {
        type: Array,
        default: () => []
    }
  },
  data() {
    return {
        slideIndices: {},
    };
  },
  computed: {
    getClass() {
      return function (index) {
        const chosen = this.slideIndices[this.slideName];
        if (index == chosen) {
          return "active";
        } else if (index == chosen + 1 || index == chosen - 1) {
          return "secondary";
        }
        return "";
      };
    },
  },
  methods: {
    onHover(index) {
      this.slideIndices = { ...this.slideIndices, [this.slideName]: index };
    },
    resetSlide() {
      this.slideIndices = { ...this.slideIndices, [this.slideName]: SLIDE_INDICES[this.slideName] };
    },
  },
  mounted() {
    this.slideIndices = { ...SLIDE_INDICES };
  }
};
</script>
    