<template>
  <div
    :id="galleryID"
    :class="className"
    class="w-100 overflow-hidden clear-float img-viewer"
  >
    <a
      v-for="(image, key) in imagesData"
      :key="key"
      :href="image.largeURL"
      target="_blank"
      rel="noreferrer"
      :data-pswp-width="image.width"
      :data-pswp-height="image.height"
      :class="key == imagesData.length - 1 ? 'mr-0' : ''"
      class="d-block quartor card p-4 float-left position-relative img-viewer-item"
    >
      <img class="w-100" :src="image.thumbnailURL" alt="" loading="lazy" />
      <p class="position-absolute img-viewer-scale text-white"><img :src="hashUrl('scale.svg')" loading="lazy"/></p>
    </a>
  </div>
</template>
  
  <script>
import PhotoSwipeLightbox from "photoswipe/lightbox";
import "photoswipe/style.css";

export default {
  name: "SimpleGallery",
  props: {
    galleryID: String,
    images: Array,
    className: String,
  },
  setup(props) {
    return {
      imagesData: props.images,
    };
  },
  mounted() {
    if (!this.lightbox) {
      this.lightbox = new PhotoSwipeLightbox({
        gallery: "#" + this.$props.galleryID,
        children: "a",
        pswpModule: () => import("photoswipe"),
      });
      this.lightbox.init();
    }
  },
  unmounted() {
    if (this.lightbox) {
      this.lightbox.destroy();
      this.lightbox = null;
    }
  },
};
</script>

<style lang="scss">
  .img-viewer-item {
    img {
      transition: all linear .3s;
    }
    .img-viewer-scale {
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      display: flex;
      align-items: center;
      justify-content: center;
      background-color: #000000b0;
      opacity: 0;
      transition: all linear .3s;
    }
    &:hover {
      .img-viewer-scale {
        opacity: 1;
      }
      img {
        transform: scale(1.3);
      }
    }
  }
</style>
  