<template>
  <div class="project page-container overflow-hidden">
    <template v-for="(v, i) in currentPorject">
      <recursive-render :key="i" :node="v"></recursive-render>
    </template>
    <div class="switch-project d-flex justify-content-center">
      <span
        @click="toPrev"
        v-if="projectIndex > 0"
        class="fs-20 hover-opacity-1"
        ><img loading="lazy" :src="hashUrl('aftersale/arrowleft.png')" /> Prev</span
      >
      <span
        @click="toNext"
        v-if="projectIndex < PROJECT_INDICES.length - 1"
        class="fs-20 hover-opacity-1"
        >Next <img loading="lazy" :src="hashUrl('aftersale/arrowright.png')"
      /></span>
    </div>
  </div>
</template>
      
      <script>
import RecursiveRender from "@/components/recursive-render.vue";
import { PROJECTS, PROJECT_INDICES } from "@/constant";

export default {
  name: "ProjectDetail",
  components: {
    
    RecursiveRender,
  },
  data() {
    return {
      PROJECTS,
      PROJECT_INDICES,
      projectIndex: -1,
      projectName: "",
      currentPorject: [],
    };
  },
  provide() {
    // 将父组件的方法提供给子组件使用
    return {
        // resetSlide: this.resetSlide
    };
  },
  methods: {
    toPrev() {
      if (this.projectIndex == 0) return;
      const str = this.PROJECT_INDICES[this.projectIndex - 1];
      this.$router.push(str);
    },
    toNext() {
      if (this.projectIndex >= this.PROJECT_INDICES.length - 1) return;
      const str = this.PROJECT_INDICES[this.projectIndex + 1];
      this.$router.push(str);
    },
    fetchData() {
      this.currentPorject = [...this.PROJECTS[this.projectName]];
      this.projectIndex = this.PROJECT_INDICES.indexOf(this.projectName);
    },
  },
  mounted() {
    const {
      params: { projectName },
    } = this.$route;
    this.projectName = projectName;
    this.fetchData();
  },
  beforeRouteUpdate(to, from, next) {
    const { projectName } = to.params;
    this.projectName = projectName;
    !!projectName && this.fetchData();
    next();
  },
};
</script>
      
<style lang="scss">
@import "../style/project.scss";
</style>
      